import React, { useState } from 'react';

export default function Interests() {
  const [interestTab, setInterestTab] = useState('boxe');

  const containerStyle = {
    display: 'flex',
    flexDirection: 'row',
    gap: '20px',
    alignItems: 'center',
    flexWrap: 'wrap',
  };

  const responsiveStyle = {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  };

  const contentStyle = {
    fontStyle: 'italic',
    borderLeft: '4px solid #ccc',
    paddingLeft: '10px',
    color: '#555',
    margin: 0,
    textAlign: 'justify',
  };

  const quoteStyle = {
    borderTop: '1px solid #ddd',
    paddingTop: '15px',
    fontStyle: 'italic',
    color: '#333',
    maxWidth: '400px',
    marginTop: '10px',
    textAlign: 'right',
    fontSize: '0.95rem',
  };

  const imageStyle = {
    borderRadius: '30px',
    maxWidth: '200px',
    height: 'auto',
    flexShrink: 0,
  };

  const isMobile = window.innerWidth < 768;

  return (
    <div>
      {/* Boutons */}
      <div style={{ display: 'flex', gap: '10px', marginBottom: '20px', flexWrap: 'wrap' }}>
        {[
          { label: 'Boxe ', value: 'boxe' },
          { label: 'Informatique ', value: 'info' },
          { label: 'Cryptomonnaie ', value: 'cryptomonnaie' },
          { label: 'Anime ', value: 'anime' },
        ].map(({ label, value }) => (
          <button
            key={value}
            onClick={() => setInterestTab(value)}
            style={{
              padding: '8px 14px',
              border: '1px solid #ccc',
              background: interestTab === value ? '#eee' : '#fff',
              cursor: 'pointer',
              borderRadius: '6px',
              fontWeight: 'bold',
            }}
          >
            {label}
          </button>
        ))}
      </div>

      {/* Contenu selon l'onglet slectionn */}
      {interestTab === 'boxe' && (
        <div style={isMobile ? responsiveStyle : containerStyle}>
          <img
            src="https://media3.giphy.com/media/v1.Y2lkPTc5MGI3NjExZmNvcjRwYmpuYjkzZHVtYnN4NmRzMGN1Y3hnNXJkcWtjbWcxcHlxciZlcD12MV9naWZzX3NlYXJjaCZjdD1n/eruVMzXlb70oo/200w.webp"
            alt="Boxe GIF"
            style={imageStyle}
          />
          <div>
            <blockquote style={contentStyle}>
              "La pratique rgulire de la boxe anglaise mapporte bien plus quun simple exercice physique : elle dveloppe ma discipline, renforce ma rsilience mentale et menseigne la matrise de soi face  ladversit. Cest un sport complet qui faonne autant le corps que lesprit."
            </blockquote>
            <div style={quoteStyle}>
              <p style={{ marginBottom: '5px', fontWeight: 'bold' }}>Mike Tyson</p>
              <p>"Everybody has a plan until they get punched in the mouth."</p>
              <p style={{ fontStyle: 'normal', color: '#666', fontSize: '0.9rem' }}>
                (Tout le monde a un plan jusqu ce quil reoive un coup de poing dans la gueule.)
              </p>
            </div>
          </div>
        </div>
      )}

      {interestTab === 'info' && (
        <div style={isMobile ? responsiveStyle : containerStyle}>
          <img
            src="https://media0.giphy.com/media/v1.Y2lkPTc5MGI3NjExMXgwZHJ6aTR0M2pjcHBpemozd2xyMjEzMDRyMDl1NHBncHY1djF5ciZlcD12MV9pbnRlcm5hbF9naWZfYnlfaWQmY3Q9Zw/CTX0ivSQbI78A/giphy.gif"
            alt="Informatique GIF"
            style={imageStyle}
          />
          <div>
            <blockquote style={contentStyle}>
              "Linformatique me passionne autant dans sa dimension matrielle que logicielle. Jaime dmonter et assembler des composants, comprendre larchitecture dun ordinateur et optimiser ses performances. Ct logiciel, je prends plaisir  coder, rsoudre des problmes et crer des solutions utiles. Cest un domaine en constante volution, qui me pousse  apprendre chaque jour et  rester curieux."
            </blockquote>
            <div style={quoteStyle}>
              <p style={{ marginBottom: '5px', fontWeight: 'bold' }}>Alan Turing</p>
              <p>"Une machine peut-elle avoir de la crativit? La crativit est simplement la capacit de combiner des ides dune manire nouvelle."</p>
            </div>
          </div>
        </div>
      )}

      {interestTab === 'cryptomonnaie' && (
        <div style={isMobile ? responsiveStyle : containerStyle}>
          <img
            src="https://media3.giphy.com/media/v1.Y2lkPTc5MGI3NjExYXVtdHp6YjVhNTVxMW5pdHVlOG0wMmVoaGRqM2oxMjJzYmZjN2NwYiZlcD12MV9naWZzX3NlYXJjaCZjdD1n/xUOxfeA0KTu1Ztj5FC/200w.webp"
            alt="Cryptomonnaie GIF"
            style={imageStyle}
          />
          <div>
            <blockquote style={contentStyle}>
              "La cryptomonnaie me passionne car elle redfinit notre conception de la finance et de la confiance numrique. Japprcie explorer les technologies blockchain, comprendre les mcanismes de consensus, et suivre lvolution des cryptos dans un monde de plus en plus dcentralis."
            </blockquote>
            <div style={quoteStyle}>
              <p style={{ marginBottom: '5px', fontWeight: 'bold' }}>Satoshi Nakamoto</p>
              <p>"If you don't believe it or don't get it, I don't have the time to try to convince you, sorry."</p>
              <p style={{ fontStyle: 'normal', color: '#666', fontSize: '0.9rem' }}>
                (Si vous n'y croyez pas ou ne comprenez pas, je n'ai pas le temps d'essayer de vous convaincre, dsol.)
              </p>
            </div>
          </div>
        </div>
      )}

      {interestTab === 'anime' && (
        <div style={isMobile ? responsiveStyle : containerStyle}>
          <img
            src="https://media3.giphy.com/media/v1.Y2lkPTc5MGI3NjExbWU0enhhbWw1Y3EyaWJvYWVnZjlhN2Y2OHlrMjd5ZGhtbGdiMThsciZlcD12MV9naWZzX3NlYXJjaCZjdD1n/t0bSJQ3Qoc9ag/giphy.webp"
            alt="Anime GIF"
            style={imageStyle}
          />
          <div>
            <blockquote style={contentStyle}>
              "Les anims et mangas sont bien plus quun divertissement : ils vhiculent des valeurs de courage, de persvrance et damiti  travers des rcits souvent profonds et motionnels. Chaque univers minspire  voir la vie autrement,  rver plus grand et  repousser mes propres limites."
            </blockquote>
            <div style={quoteStyle}>
              <p style={{ marginBottom: '5px', fontWeight: 'bold' }}>Rock Lee  *Naruto*</p>
              <p>"Un loser peut surpasser un gnie avec suffisamment defforts."</p>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
